#yellow-bin {
    width: 100%;
}

#yellow-bin .carousel-caption {
    left: 0;
    width: max(25%, 18rem);
    height: 100%;
    bottom: 0;
    background-image: none;
    background-color: transparent;
    z-index: 1;
    color: darkgray;
    backdrop-filter: none;

}

#yellow-bin .carousel-caption::after {
    content: "";
    position: absolute;
    left: -1rem;
    top: 0;
    bottom: -1rem;
    width: 100%;
    height: 110%;
    background-color: rgba(255,255,255,.6);

    background-image: linear-gradient(to left, #393d41d0 0%, #393d41a2 50%, #393d4138 100%), url('../..//resource/background/home/yellowBin.png');

    filter: blur(1rem);
    background-repeat:no-repeat;
    background-size:cover;
    background-position:right bottom;
    z-index: -1;
}

#yellow-bin h3 {
    position: absolute;
    top: 55%;
    right: -6rem;
    rotate :90deg;
    white-space: nowrap;
    font-size: 350%;
}

#yellow-bin p {
    position: absolute;
    rotate: 90deg;
    top: 20%;
    right: -3rem;
    white-space: nowrap;
    font-size: 200%;
}

#yellow-bin p span{
    display: flex;
}

